@keyframes gradientAnimation {
    0% {
      background-color: #FFF;
    }
    20% {
        background-color: #FFF;
    }
    40% {
      background-color: #FFF3D9;
    }
    60% {
      background-color: #FFF3D9;
    }
    80% {
        background-color: #FFF3D9;
      }
      100% {
        background-color: #FFF;
      }
}

  
.body {
    overflow-x: hidden;
    animation: gradientAnimation 6s ease infinite;
    box-shadow: 0 0 15px #ccc;
}


.f-13-l-40 {
    font-size: 13px;
