.font-sitka-it{
    font-family: 'SITKAVF-ITALIC', sans-serif;
}

.ml-mr-20{
    margin: 0 auto; 
    max-width: 1400px;
}
.body {
    font-family: 'SITKAVF', sans-serif;
}

.mt-50{
    margin-top: 50px;
}

.mb-50{
    margin-bottom: 120px;
}

.t-color-C68800{
    color: #C68800;
}

.t-color-0{
    color: #000000;
}

.t-color-FFF{
    color: #FFFFFF;
}

a{
    text-decoration: none;
    color: #000000;
}

.logo{
    width: 120px;
}

.anim-img {
    opacity: 0;
    transform: scale(0.5);
    animation: imgFadeIn 2s ease-in-out forwards, imgShake 4s infinite alternate; 
}

@keyframes imgFadeIn {
    0% {
        opacity: 0;
        transform: scale(0.5);
    }
    100% {
        opacity: 1;
        transform: scale(1);
    }
}

@keyframes imgShake {
    to {
        transform: translateY(10px);
    }
    from {
        transform: translateY(-10px);
    }
}

.anim-button {
    transition: transform 0.3s ease;
    cursor: pointer;
    animation: pulse 2s infinite alternate;
}

@keyframes pulse {
    to {
        transform: scale(1.1);
    }
}

.header{
    background-color: white;
    display: flex;
    justify-content: center;
    align-items: center;
    box-shadow: 0 0 15px #ccc;
}

.menu-items{
    width: 1150px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 10px;
    position: sticky;
}

.honey-header{
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    height: 500px;
    margin-bottom: 120px;
    margin-top: 50px;
    border: 1px solid rgb(204, 204, 204, 0.5);
    border-radius: 20px;
}

.honey-header div{
    width: 455px;
}

.honey-header img{
    width: 547px;
    height: 469px;
}

.honey-header-h3{
    width: 300px;
    height: 41px;
    background-color: #FFF1D0;
    border-radius: 15px;
    text-align: center;
    line-height: 40px;
    padding-left: 20px;
    padding-right: 20px;
}

.contact-us-btn-top{
    background: radial-gradient(circle at center, #F4C04F, #EFA606);
    color: #FFFFFF;
    border: none;
    border-radius: 10px;
    height: 55px;
    width: 195px;
    margin-top: 25px;
}

.contact-us-btn-top>a{
    color: #FFFFFF;
}

.contact-us-btn-bot{
    background: radial-gradient(circle at center, #F4C04F, #EFA606);
    color: #FFFFFF;
    border: none;
    padding-top: 20px;
    padding-bottom: 20px;
    padding-left: 30px;
    padding-right: 30px;
    border-radius: 10px;
    margin-top: 25px;
}

.contact-us-btn-bot>a{
    color: #FFFFFF;
}

.about-us{
    display: flex;
    flex-direction: row;
    align-items: flex-end;
    justify-content: space-around;
    margin-bottom: 120px;
}

.about-us-item{
    width: 465px;
}

.about-us-item>h2{
    background: linear-gradient(180deg, #30a3ff, #30a3ff 50%, #F4C04F 50%);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
}

.ukranian-honey{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin-bottom: 50px;
}

.ukranian-honey-header{
    text-align: center;
}

.ukranian-honey-item{
    max-width: 1400px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 10px;
}

.ukranian-honey-p{
    width: 550px;
    height: 310px;
}

.our-bees-and-hives-container{
    display: flex;
    flex-direction: column;
    justify-content: center;
}

.our-bees-and-hives-container-item{
    text-align: center;
    margin: auto;
}

.our-bees-and-hives-item{
    max-width: 1100px;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    margin-top: 120px;
    margin-left: 120px;
}

.our-bees-and-hives-item img{
    width: 450px;
    height: 100%;
}

.our-bees-and-hives-item div{
    width: 500px;
}

.imgContainer {
    max-width: 1150px;
    height: 400px;
    display: flex;
    flex-direction: row;
    margin: auto;
}

.imgContainerItem {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    width: 800px;
    height: 400px;
}

.imgContainerItem img {
    border-radius: 30px;
    height: 200px;
    margin-bottom: 10px;
    margin-right: 10px;
    object-fit: cover;
    width: calc(50% - 10px)
}

.sideContainer {
    width: 400px;
    height: 400px;
}

.sideContainer img {
    border-radius: 30px;
    width: 400px;
    height: 410px;
    object-fit: cover;
}

.our-service-container{
    display: flex;
    justify-content: row;
    align-items: flex-end;
    justify-content: space-around;
    padding-top: 210px;
}

.our-service-container img{
    width: 550px;
}

.our-service{
    width: 440px;
    margin-bottom: 30px;
}

.our-service-container>div>h2{
    background: linear-gradient(180deg, #30a3ff, #30a3ff 50%, #F4C04F 50%);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
}
.our-production-container{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

input,
button,
textarea,
select {
    font: inherit;
}

.mb-20 { margin-bottom: 20px; }
.mb-40 { margin-bottom: 40px }


.medove-form {
    display: flex;
    position: relative;
    margin: auto;
    margin-bottom: 50px;
    width: calc(960px - 128px);
    height: calc(320px - 96px);
    background: radial-gradient(circle at center,#f4c04f,#efa606);
    padding: 48px 64px;
    border-radius: 60px;
    overflow: hidden;
}

.medove-form-text {
    width: 462px;
    height: 128px;
}

.medove-form-left-part {
    align-content: end;
    z-index: 1;
}

.medove-inputs {
    width: calc(195px - 30px);
    height: calc(55px -30px);
    margin-right: 10px;
    padding: 15px;
    border-radius: 10px;
}

.medove-submit-btn {
    color: #FFFFFF;
    font-size: 15px;
    width: 195px;
    height: 55px;
    background: #442E00;
    border-radius: 10px;
}

.medove-form-pic {
    position: absolute;
    top: 0;
    right: 0;
    border-radius: 0 60px 60px 0;
    z-index: 0;
}


.footer-bg-color{
    background-color: #442E00;
}

.footer{
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    padding-top: 30px;
    padding-bottom: 30px;
    position: relative;
    width: 960px;
}

.call{
    width: 150px;
    margin-top: 17px;
}

.map>iframe{
    width: 150px;
    height: 80px;
    border-radius: 20px;
}

.arrow-top-btn{
    display: flex;
    align-items: center;
    justify-content: center;
    width: 34px;
    height: 34px;
    border: none;
    border-radius: 10px;
    background: radial-gradient(circle at center, #F4C04F, #EFA606);
    position: absolute;
    top: 50%;
    right: -100px;
}

.arrow-top-btn a {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100%;
    height: 100%;
    text-decoration: none;
}

.arrow-top-btn img {
    max-width: 100%;
    max-height: 100%;
}

.menu-toggle{
    display: none;
}

.toggle-img{
    display: none;
}



