    .menu-toggle {
        display: block; 
        cursor: pointer;
        position: absolute;
        top: 15px;
        left: 20px;
    }

    .toggle-img{
        display: block;
        margin: auto;
    }
    
    .bar {
        width: 25px;
        height: 3px;
        background-color: #333;
        margin: 5px 0;
        transition: 0.4s;
    }
    
    .menu-toggle.active .bar:nth-child(1) {
        transform: rotate(-45deg) translate(-5px, 6px);
    }
    
    .menu-toggle.active .bar:nth-child(2) {
        opacity: 0;
    }
    
    .menu-toggle.active .bar:nth-child(3) {
        transform: rotate(45deg) translate(-5px, -6px);
    }
    
    .menu-items.active {
        display: block;
    }

